import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store = new Vuex.Store({
	state:{
		
	},
	getters:{
		//根据参数获取队伍数据
		getCamp:(state,getters) => (camp) =>{
			if(camp == "red"){
				return sessionStorage.sessionRedCamp.split(',');
			}
			if(camp == "blue"){
				return sessionStorage.sessionBlueCamp.split(',');
			}
			return null;
		}
	},
	mutations:{
		
	}
})

export default store


